import { Link } from "react-router-dom";
import styled from "styled-components";

export const CharacterContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 400px));
    justify-content: center;
    grid-gap: 70px;
    text-align: center;
    padding: 40px;

    @media (max-width: 767px) {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        padding: 20px;
    }
`;

export const CharacterWrapper = styled(Link)`
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    padding: 40px;
    position: relative;
    text-decoration: none;
    color: white;
    border-radius: 40px;
    border: 1px solid black;
    box-shadow: 4px 4px 12px 0px rgba(66, 68, 90, 1);
    cursor: pointer;
    transition: all .3s ease;

    &:hover {
        transform: translateY(-10px) scale(1.1);
        box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
        filter: brightness(110%);
    }

    @media (max-width: 767px) {
        padding: 20px;
    }
`;

export const CharacterName = styled.h3`
    margin: 0;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 24px;
    letter-spacing: 2px;
    align-self: flex-end;
    background: rgba(66, 68, 90, .8);
    border-radius: 20px;

    @media (max-width: 767px) {
        font-size: 20px;
    }
`;

export const Image = styled.img`
    border-radius: 50%;
    width: 100px;
    box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
    border: 1px solid black;
    position: absolute;
    top: -50px;
    left: -30px;

    @media (max-width: 767px) {
        width: 80px;
        left: -15px
    }
`;

export const CharacterData = styled.div`
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 20px;
    font-size: 20px;
    line-height: 1.7;
    letter-spacing: 3px;
    border-radius: 20px;
    background: rgba(66, 68, 90, .5);

    @media (max-width: 767px) {
        font-size: 14px;
    }
`;

export const CharacterType = styled.p`
    margin: 0;
`;

export const FormWrapper = styled.div`
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
    padding: 40px 80px;
    margin-bottom: 40px;
`;

export const SelectContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
`;

export const LabelSelect = styled.label`
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 10px;
   width: 150px;
`;

export const LabelName = styled.span`
    text-transform: capitalize;
    background: rgba(66, 68, 90, .5);
    border-radius: 20px;
    padding: 5px 10px;
    letter-spacing: 2px;
    color: white;
`;

export const Select = styled.select`
    width: 100%;
    color: white;
    letter-spacing: 1.7px;
    padding: 10px 15px;
    border-radius: 40px;
    box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
    border: 1px solid black;
    background: rgba(66, 68, 90, .5);
    cursor: pointer;
`;