import { FormWrapper, SelectContainer, LabelSelect, LabelName, Select } from './styled';
import { useLocation } from "react-router-dom";
import { filterData } from "../../utils/filterData";
import { useEffect } from "react";
import { useGlobalContext } from '../../context/global-context';
import { Button } from '../../common/styles/Button';
import { Input } from '../../common/styles/Input';

const SearchCharacter = () => {
    const { setPage, filters, handleFilterChange, handleClear } = useGlobalContext();
    const location = useLocation();

    useEffect(() => {
        setPage(1);
    }, [filters, location]);

    return (
        <FormWrapper>
            <label htmlFor="name-input">
                <Input id="name-input" type="text" name="name" placeholder="Search name..." value={filters.name} onChange={handleFilterChange} />
            </label>
            <SelectContainer>
                {filterData.map(({ name, options }) => (
                    <LabelSelect key={name} htmlFor={`${name}-select`}>
                        <LabelName>{name}: </LabelName>
                        <Select
                            name={name}
                            id={`${name}-select`}
                            value={filters[name]}
                            onChange={handleFilterChange}
                        >
                            {options.map((option) => (
                                <option key={option} value={option}>
                                    {option}
                                </option>
                            ))}
                        </Select>
                    </LabelSelect>
                ))}
            </SelectContainer>
            <Button onClick={handleClear}>Clear</Button>
        </FormWrapper>
    )
};

export default SearchCharacter;