import styled, { css } from "styled-components";

export const LocationsContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 600px));
    justify-content: center;
    grid-gap: 40px;
    padding: 40px;

    @media (max-width: 767px) {
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
        padding: 20px;
    }
`;

export const LocationWrapper = styled.div`
    display: flex;
    background-color: transparent;
    flex-direction: column;
    gap: 20px;
    padding: 40px 60px;
    position: relative;
    border: 1px solid black;
    border-radius: 40px;
    box-shadow: 4px 4px 12px 0px rgba(66, 68, 90, 1);
    text-align: center;
`;

export const LocationNumber = styled.span`
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(217,209,198);
    font-size: 28px;
    font-weight: 600;
    border-radius: 50%;
    box-shadow: 4px 4px 12px 0px rgba(66, 68, 90, 1);
    position: absolute;
    top: -15px;
    left: -15px;
`;

export const LocationName = styled.h3`
    font-size: 24px;
    letter-spacing: 2px;
    background: rgba(66, 68, 90, .8);
    color: rgb(217,209,198);
    box-shadow: 4px 4px 12px 0px rgba(66, 68, 90, 1);
    border-radius: 10px;
    padding: 10px;

    @media (max-width: 767px) {
        font-size: 20px;
    }
`;

export const LocationData = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
    font-size: 20px;
    
    @media (max-width: 767px) {
        font-size: 14px;
    }
`;

export const LocationType = styled.p`
    padding: 10px;
    border-radius: 10px;
    background: rgba(66, 68, 90, .9);
    box-shadow: 4px 4px 12px 0px rgba(66, 68, 90, 1);
    color: rgb(217,209,198);
    font-weight: 600;
    letter-spacing: 3.5px;
`;

export const SearchContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`;

export const ButtonsWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;
    gap: 15px;
`;

export const TypeButton = styled.button`
    padding: 10px 15px;
    border-radius: 40px;
    color: white;
    letter-spacing: 3px;
    background-color: rgba(66, 68, 90, .5);
    cursor: pointer;
    transition: all .3s ease;
    border: 2px solid transparent;

    ${({ active }) => active && css`
        border: 2px solid white;
        box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
    `}

    &:hover {
        transform: scale(1.1);
        filter: brightness(120%);
        box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
    }
`;

export const ResidentsList = styled.div`
   display: flex;
   gap: 5px;
   flex-wrap: wrap;
   margin-top: 20px;;
`;